button {
  width: 150px;
  height: 70px;
  color: #000;
  text-transform: uppercase;
  border: 2px solid #9294f8;
  border-radius: 10px;
  font-size: 2rem;
  text-decoration: none;
  position: relative;
  left: 40%;
  overflow: hidden;
}

button:hover {
  box-shadow: 1px 1px 25px 10px rgba(146, 148, 248, .4);
}

button:before {
  content: "";
  width: 100%;
  height: 100%;
  background: linear-gradient(120deg, rgba(0, 0, 0, 0), rgba(146, 148, 248, .4), rgba(0, 0, 0, 0));
  transition: all .65s;
  position: absolute;
  top: 0;
  left: -100%;
}

button:hover:before {
  left: 100%;
}

